<template>
  <van-dialog v-model="show"  class="pay"   cancelButtonColor="#1988FF" title="充值成功" 	 @confirm="ConfirmClosing">
  </van-dialog>
</template>

<script>
  export default {
    name: "rechargeState",
    data(){
      return{
        show:true
      }
    },
    methods:{
      ConfirmClosing(){
        this.$router.push({
          name:'my'
        })
      },
      noShowSheet(){
        this.$router.push({
          name:'my'
        })
      }
    }
  }
</script>

<style scoped lang="scss">
  @import "../../../style/my";
</style>



